@font-face {
  font-family: "TT Lakes";
  src: url("./fonts/TTLakes-Regular.ttf") format("ttf"), url("./fonts/TTLakes-Regular.eot") format("eot"), url("./fonts/TTLakes-Regular.woff") format("woff");
  font-weight: 400;
}
@font-face {
  font-family: "TT Lakes";
  src: url("./fonts/TTLakes-Medium.ttf") format("ttf"), url("./fonts/TTLakes-Medium.eot") format("eot"), url("./fonts/TTLakes-Medium.woff") format("woff");
  font-weight: 500;
}
@font-face {
  font-family: "TT Lakes";
  src: url("./fonts/TTLakes-Bold.ttf") format("ttf"), url("./fonts/TTLakes-Bold.eot") format("eot"), url("./fonts/TTLakes-Bold.woff") format("woff");
  font-weight: 700;
}
body {
  background-color: #f8f8f8;
  padding: 0;
  margin: 0 auto;
  font-family: "TT Lakes";
  max-width: 1440px;
}

.main-caption {
  margin-left: 1rem;
}

.main-caption::before {
  position: relative;
  left: -1rem;
  border-radius: 0 25% 25% 0;
  background: #41f6d7;
  height: 2.5rem;
  width: 0.25rem;
}

header {
  background-color: #fff;
  padding: 0 1rem;
}

.nav {
  height: 5.5rem;
  padding: 0;
  display: grid;
  gap: 1rem;
  grid-template-columns: 2.5rem 6rem 2px auto 2.5rem 2.5rem;
  align-items: center;
  box-sizing: border-box;
}

.nav__tablet,
.nav__desktop {
  display: none;
}

@media (min-width: 768px) {
  .nav__tablet {
    display: block;
    padding-top: 1.5rem;
  }
  .nav__around {
    order: -1;
  }
  .nav__menu {
    order: -2;
  }
  .nav {
    grid-template-columns: 2.5rem 2px 6rem auto 2.5rem 2.5rem 2.5rem 2px 2.5rem 2.5rem;
  }
}
@media (min-width: 768px) {
  .nav__tablet {
    display: block;
    padding-top: 1.5rem;
  }
  .nav__around {
    order: -1;
  }
  .nav__menu {
    order: -2;
  }
  .nav {
    grid-template-columns: 2.5rem 2px 6rem auto 2.5rem 2.5rem 2.5rem 2px 2.5rem 2.5rem;
  }
}
header._blur {
  filter: blur(5px);
}

@media (min-width: 1120px) {
  .nav__desktop {
    display: block;
  }
  .nav {
    display: flex;
    justify-content: flex-end;
    background-color: #f8f8f8;
  }
  .nav::after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 1rem;
    height: 5.5rem;
    background-color: #f8f8f8;
  }
  .nav__tablet,
  .nav__mobile {
    display: none;
  }
}
.services__buttons {
  display: flex;
  overflow: scroll;
  align-items: stretch;
  flex-wrap: nowrap;
  gap: 0.625rem;
  padding-left: 0.625rem;
  padding-right: 0.625rem;
}

.services__button {
  flex-shrink: 0;
  background: none;
  border: 2px solid #f8f8f8;
  border-radius: 6px;
  margin: 0;
  padding: 0.625rem;
  width: auto;
  height: 2.5rem;
  font-size: 1rem;
  color: #7e7e82;
}

.services__button:hover {
  border: 2px solid #b8ffec;
  border-radius: 6px;
  background-color: #fff;
}

.services__title {
  display: flex;
  gap: 0.75rem;
}

@media (min-width: 1120px) {
  .services__buttons {
    overflow: auto;
    flex-wrap: wrap;
  }
}
.main-text-img {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
}

@media (min-width: 768px) {
  .main-text-img {
    flex-direction: row;
  }
}
.main-text-img__picture {
  flex: auto;
  object-fit: contain;
}

.main-text-img__text--text {
  overflow: hidden;
  max-height: 3.5rem;
}

.main-text-img__text--text._show-more {
  max-height: 9999999rem;
  transition: all 0.5s ease 0s;
}

.main-text-img__text--button {
  background-color: #f8f8f8;
  border: none;
  align-self: start;
  font-weight: 500;
}

.main-text-img__text--button--text--no {
  display: none;
}

.js-main-text-img__text--button--text {
  display: none;
}

.js-main-text-img__text--button--text--no {
  display: inline;
}

.js-main-text-img__text--button--img {
  transform: rotate(180deg);
}

.additional-information {
  padding-left: 1rem;
  padding-top: 2rem;
}

.additional-information__text {
  font-family: "TT Lakes";
  font-size: 14px;
  line-height: 20px;
  /* or 143% */
  letter-spacing: 0.2px;
  color: #1b1c21;
}

.additional-information__link {
  margin-top: -1rem;
}

.additional-information__link--text {
  font-size: 16px;
  line-height: 24px;
  /* identical to box height, or 150% */
  letter-spacing: -0.2px;
  color: #1b1c21;
  font-weight: 500;
}

main {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}

main._blur {
  filter: blur(6px);
}

@media (min-width: 1120px) {
  main {
    position: relative;
    transform: translate(0, -6rem);
  }
  .the-most-important-class {
    background: rgb(248, 248, 248);
    background: linear-gradient(90deg, rgb(255, 255, 255) 20rem, rgb(255, 255, 255) 20rem, rgb(248, 248, 248) 20rem, rgb(248, 248, 248) 100%);
  }
}
body {
  transform: translate(0, 0);
}

.id-of-side-panel {
  width: 100vw;
  min-width: 320px;
  height: 100vh;
  max-height: 1024px;
  margin: 0;
  padding: 0rem 1rem 2rem 1rem;
  box-sizing: border-box;
  position: absolute;
  top: 0rem;
  left: 0;
  background-color: #fff;
  z-index: 99;
  transform: translateX(-100%);
  transition: all 0.6s ease 0s;
}

.id-of-side-panel._open {
  transform: translateX(0);
}

body._overflow-hidden {
  overflow: hidden;
}

.side-panel__button {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

.side-panel__button--text {
  font-weight: medium;
  background: none;
  border: none;
  width: auto;
  height: 40px;
  margin-right: auto;
  position: relative;
  left: -2rem;
  padding-left: 2rem;
}

.side-panel__button--text:hover {
  color: #7e7e82;
  background: url("img/service-left.svg") no-repeat 1rem;
}

.side-panel__button:hover {
  color: #7e7e82;
}

.side-panel {
  display: grid;
  grid-template-rows: 10% 1fr 20%;
}

.side-panel-buttons--top {
  height: 5.5rem;
  display: grid;
  grid-template-columns: 40px 100px 1fr;
  gap: 1rem;
  align-items: center;
  padding-bottom: 3rem;
}

.side-panel__search {
  justify-self: end;
}

.side-panel-buttons--middle {
  height: 60%;
  display: grid;
  grid-template-rows: repeat(7, 3rem);
}

.side-panel-buttons--middle * {
  flex: auto;
}

.side-panel-buttons--bottom {
  height: 20%;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(4, 40px) 1fr;
  grid-template-rows: repeat(4, 3rem);
}

.side-panel__mail {
  text-decoration: none;
  color: #000;
  grid-area: 2/1/2/6;
}

.side-panel__number {
  grid-area: 2/1/2/6;
}

.side-panel__lang--ru {
  grid-area: 3/1/3/2;
}

.side-panel__lang--en {
  grid-area: 3/2/3/3;
}

.side-panel__lang--cn {
  grid-area: 3/3/3/4;
}

@media (min-width: 768px) {
  .id-of-side-panel {
    max-width: 20rem;
  }
}
@media (min-width: 1120px) {
  .id-of-side-panel {
    transition: none;
    transform: translateX(0);
    position: relative;
    top: -5.5rem;
    float: left;
  }
  .side-panel__cross {
    visibility: hidden;
  }
  .side-panel__logo {
    order: -1;
  }
}
@media (max-width: 767px) {
  .repair-of-brands--tablet {
    display: none;
  }
  .repair-of-brands {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    min-width: 0;
    overflow: hidden;
    background: #f8f8f8;
    background-size: 100%;
  }
  .repair-of-brands__text {
    padding: 1rem;
    display: block;
    height: 80px;
    box-sizing: border-box;
    font-family: "TT Lakes";
    font-size: 1rem;
    line-height: 1.5rem;
    letter-spacing: -0.2px;
    text-transform: uppercase;
    color: #7e7e82;
    background: rgb(248, 248, 248);
    background: linear-gradient(90deg, rgb(248, 248, 248) 0%, rgb(255, 255, 255) 100%);
    border-top: 1px solid #d9fff5;
  }
  .repair-of-brands__text {
    padding-bottom: 2rem;
  }
  .swiper {
    min-width: 0;
    max-width: 100%;
    margin-top: 0;
    padding: 1rem;
  }
  .image-slider__image {
    height: 4.5rem;
    background: #ffffff;
    border: 1px solid #eaeaea;
    border-radius: 0.375rem;
    box-sizing: border-box;
    width: 100%;
    min-width: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
  }
  .image-slider__image img {
    height: 2rem;
  }
  .swiper-pagination {
    position: relative;
    padding-top: 0;
    padding: 1rem;
  }
  /* пагинация */
  .swiper-pagination-bullet {
    background-color: #b5b6bc;
    width: 0.75rem;
    height: 0.75rem;
  }
}
@media (min-width: 768px) {
  .repair-of-brands,
  .repair-of-brands--any-types {
    display: none;
  }
  .repair-of-brands--tablet {
    width: 100%;
    display: flex;
    flex-direction: column;
    min-width: 0;
    overflow: hidden; /* text-overflow */
    background: #f8f8f8;
    background-size: 100%;
  }
  .repair-of-brands__text--tablet {
    padding: 1rem;
    display: block;
    height: 80px;
    box-sizing: border-box;
    font-family: "TT Lakes";
    font-size: 1rem;
    line-height: 1.5rem;
    letter-spacing: -0.2px;
    text-transform: uppercase;
    color: #7e7e82;
    background: rgb(248, 248, 248);
    background: linear-gradient(90deg, rgb(248, 248, 248) 0%, rgb(255, 255, 255) 100%);
    border-top: 1px solid #d9fff5;
  }
  .image-slider__slide--tablet {
    flex: auto;
  }
  .repair-of-brands__text--tablet {
    padding-bottom: 2rem;
  }
  .image-slider__wrapper--tablet {
    padding: 1rem;
    display: flex;
    flex-direction: row;
    gap: 1rem;
    flex-wrap: wrap;
    height: 10rem;
    overflow: hidden;
  }
  .image-slider__image--tablet {
    height: 4.5rem;
    background: #fff;
    border: 1px solid #eaeaea;
    border-radius: 0.375rem;
    box-sizing: border-box;
    max-width: 100%;
    min-width: 14rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem 0 1rem;
  }
  .swiper--readmore--tablet {
    background-color: #f8f8f8;
    border: none;
    padding-left: 1rem;
    /* transform: translate(0, 1rem); */
    align-self: start;
  }
  /* .js-text-more {
      overflow: visible;
  } */
  /* .js-button-more {
      transform: translate(0, 5.5rem);
  } */
  .js-img__read-more--tablet {
    transform: rotate(180deg);
  }
  .read-more--text--tablet {
    font-weight: 500;
  }
  .read-more--text--no--tablet {
    display: none;
    font-weight: 500;
  }
  .js-read-more--text--tablet {
    display: none;
  }
  .js-read-more--text--no--tablet {
    display: inline;
  }
  .js-image-slider__wrapper--tablet {
    height: auto;
  }
}
@media (min-width: 768px) {
  .repair-of-brands {
    display: none;
  }
  .image-swiper-any-type--text {
    color: #1b1c21;
  }
  .image-slider__wrapper--tablet--any-types {
    padding: 1rem;
    display: flex;
    flex-direction: row;
    gap: 1rem;
